import React, { useEffect, useState } from "react";
import "./Sidenav.css";
import { FaLinkedin, FaGithub } from "react-icons/fa";

const Sidenav = () => {
  const [contactVisible, setContactVisible] = useState(false); // Estado para detectar si la seccin de contacto est visible
  const [isMobile, setIsMobile] = useState(false); // Estado para detectar si la pantalla es pequea

  const handleScroll = () => {
    const contactSection = document.getElementById("contact"); // Asegrate de que el id de tu seccin de contacto sea "contact"
    const rect = contactSection.getBoundingClientRect(); // Obtener la posicin de la seccin en la pantalla
    
    // Para cambiar el color cuando la mitad de la seccin est visible
    const threshold = window.innerHeight / 1.8; // Puedes ajustar este valor

    // Verificar si el centro de la seccin de contacto est dentro del viewport
    if (rect.top <= threshold && rect.bottom >= threshold) {
      setContactVisible(true); // La seccin de contacto est visible segn el umbral
    } else {
      setContactVisible(false); // La seccin de contacto no est visible
    }
  };

  const checkIfMobile = () => {
    // Detectar pantallas pequeas (menos de 768px de ancho)
    if (window.innerWidth <= 768) {
      setIsMobile(true);
    } else {
      setIsMobile(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll); // Agregar el evento de scroll
    window.addEventListener("resize", checkIfMobile); // Agregar el evento de cambio de tamao para pantallas pequeas
    checkIfMobile(); // Verificar el tamao de pantalla al cargar el componente
    return () => {
      window.removeEventListener("scroll", handleScroll); // Limpiar el evento cuando el componente se desmonte
      window.removeEventListener("resize", checkIfMobile); // Limpiar el evento de cambio de tamao
    };
  }, []);

  return (
    <div
      id="sidenav"
      className={isMobile ? "mobile" : ""}
    >
      <a
        href="https://www.linkedin.com/in/tu-perfil"
        target="_blank"
        rel="noopener noreferrer"
        className={contactVisible ? "active" : ""}
      >
        <FaLinkedin />
      </a>
      <a
        href="https://github.com/MitziVite"
        target="_blank"
        rel="noopener noreferrer"
        className={contactVisible ? "active" : ""}
      >
        <FaGithub />
      </a>
    </div>
  );
};

export default Sidenav;
